.portfolio {
    margin-top: 87px;
    width: 100%;
}
  
.portfolio__title {
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 18px;
    line-height: 111%;
    color: #8b8b8b;
}

.portfolio__element {
    font-weight: 400;
    font-size: 30px;
    line-height: 167%;
    letter-spacing: -0.04em;
}

.portfolio__element:last-child {
    border-bottom: none;
    margin-bottom: 105px;
}
  
  
.portfolio__link {
    height: 89px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #424242;
    font-weight: 400;
    font-size: 30px;
    line-height: 167%;
    letter-spacing: -0.04em;
}
  
.portfolio__link-image {
    background-image: url('../../../images/arrow.svg');
    background-repeat: no-repeat;
    background-position: center;
    width: 30px;
    height: 30px;
}
  
  @media screen and (max-width: 768px) {
    .portfolio {
      margin-top: 75px;
    }
    
    .portfolio__link {
      font-weight: 400;
      font-size: 28px;
      line-height: 179%;
    }
    
    .portfolio__element:last-child {
      margin-bottom: 72px;
    }
  }
  
  @media screen and (max-width: 530px) {
    .portfolio {
      margin-top: 60px;
    }
    
    .portfolio__title {
      margin-bottom: 18px;
      font-size: 14px;
      line-height: 143%;
      color: #a0a0a0;
    }
    
    .portfolio__link {
      height: 67px;
      font-size: 18px;
      line-height: 156%;
      border-color: #e6e6e6;
    }
    
    .portfolio__element:last-child {
      margin-bottom: 50px;
    }
    
    .portfolio__link-image {
      margin-right: 3px;
      background-size: cover;
      width: 10px;
      height: 10px;
    }
  }